import Vue from 'vue'
import Router from 'vue-router'
import Sidebar from '@/components/Admin/Sidebar'



Vue.use(Router)

const routes = [
  {
    path: '',
    name: 'home',
    component: () => import('../pages/HomePage.vue')
  },
  {
    path: '/register-form',
    name: 'Register Form',
    component: () => import('../pages/RegisterForm.vue')
  },
  {
    path: '/signin-form',
    name: 'SigninForm',
    component: () => import('../pages/SigninForm.vue')
  },
  {
    path: '/service_list',
    name: 'Servicelist',
    component: () => import('../pages/Servicelist.vue')
  },
  {
    path: "/Dashboard",
    name: "Dashboard",
    component: () => import('../pages/Admin/Dashboard.vue')
    
  }    


]






export default new Router({

  routes
})



